body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

ul {
	list-style: none;
}

.App {
  background-color: #003c6c;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: left;
  justify-content: top;
  color: white;
  padding: 10px;
}

.gradient {
background-image: -webkit-linear-gradient(right, #003c6c , #fdc700);
background-image: linear-gradient(to left, #003c6c , #fdc700);
background-color: #fdc700; /* For browsers that do not support gradients */
height: 10px;
margin-top: 7px;
}

.title{
  font-family: EB Garamond;
  font-size:calc(30px + 2vmin);
  color: #fdc700;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
}

li {
    float: left;
    border-right:1px solid #bbb;
}

li:last-child {
    border-right: none;
}

.listButton {
    background-color: #003c6c;
    color: white;
    cursor: pointer;    
    padding: 10px;
    width: 100%;
    border: none;
    text-align: center;
    font-size: 15px;
    text-decoration: none;
}

.listButton:hover {
    background-color: #00266C;
}
